import { Component, OnInit, HostListener, Input } from '@angular/core';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import * as moment from 'moment';
import { TIMELINES } from '@shared/constants/property-details';
import { time } from 'd3';

@Component( {
  selector: 'timelines-content',
  templateUrl: './timelines-content.component.html',
  styleUrls: ['./timelines-content.component.scss']
} )
export class TimelinesContentComponent implements OnInit {
  @Input( 'propertyId' ) id: number;
  details;
  baseClassName;
  currentDate;
  timelinePosIndex = null;
  currentTimeline;
  summary;
  timelines;



  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
      this.baseClassName = this._viewportService.sizeClassName();
    }, 250 );
  }

  constructor (
    private _propertyDetailsService: PropertyDetailsService,
    private _viewportService: ViewportService
  ) { }

  ngOnInit () {
    this._viewportService.checkViewSizes();
    this.baseClassName = this._viewportService.sizeClassName();
    this.currentDate = this.getMonthYear();
    this.getTimelineDetails();
  }

  getTimelineDetails () {
    this._propertyDetailsService
      .getTimelineDetails( this.id )
      .subscribe( res => {
        this.timelines = res.propertyTimelines;
        this.summary = res.propertyTimeLineSummary;
        this.initTimeline();
      } );
  }


  initTimeline () {
    this.timelinePosIndex = this.getTimeLineIndex( 'present' );
    if ( this.timelinePosIndex === null ) {
      this.timelinePosIndex = this.getTimeLineIndex( 'future' );
      if ( this.timelinePosIndex === null ) {
        this.timelinePosIndex = this.getTimeLineIndex( 'past' );
      }
    }
    this.setCurrentTimeline();
  }

  setCurrentTimeline () {
    if ( this.timelinePosIndex != null ) {
      if ( this.timelinePosIndex + 4 < this.timelines.length ) {
        this.currentTimeline = this.timelines.slice( this.timelinePosIndex, this.timelinePosIndex + 4 );
      } else {
        this.currentTimeline = this.timelines.slice( this.timelinePosIndex );
      }
    }
  }

  getTimeLineIndex ( strTimeLine ) {
    let ind = null;
    this.timelines.filter( ( timeline, index ) => {
      if ( this.getTimelineKey( timeline.month, timeline.year ) === strTimeLine && ind === null ) {
        ind = index;
      }
    } );
    return ind;
  }

  moveTimeline ( direction ) {
    if ( direction === 'left' ) {
      this.timelinePosIndex--;
    } else {
      this.timelinePosIndex++;
    }
    this.setCurrentTimeline();

  }



  getTimelineKey ( month, year ) {
    if ( year >= this.currentDate.year ) {
      if ( year > this.currentDate.year ) {
        return 'future';
      } else {
        if ( month >= this.currentDate.month ) {
          if ( month > this.currentDate.month ) {
            return 'future';
          } else {
            return 'present';
          }
        } else {
          return 'past';
        }
      }
    } else {
      return 'past';
    }
  }

  getMonthYear () {
    const now = moment().format();
    return {
      month: moment( now ).format( 'M' ),
      year: moment( now ).format( 'YYYY' ),
    }
  }

  formatDate ( date ) {
    return moment( date ).format( 'DD MMM YYYY' );
  }

}
