import { Component, OnInit, Input } from '@angular/core';
import { PropertyDetailsService } from '@shared/api';

@Component({
  selector: 'financial-details-content',
  templateUrl: './financial-details-content.component.html',
  styleUrls: ['./financial-details-content.component.scss']
})
export class FinancialDetailsContentComponent implements OnInit {
  @Input() propertyId: number;
  details: any;
  financiaData;
  totalParts: number = 0;

  financialSlider: number = 0;
  netIncome;
  incomePercentage;
  expensesPercentage;
  totalPerformanceWithZeroUsage;
  incomeShare;

  filterData = 1;
  options: Array<any> = [
    {
      name: 'Projections',
      value: 1
    }
  ];

  constructor(private _propertyDetailsApi: PropertyDetailsService) { }

  ngOnInit() {
    if (!this.propertyId) {
      return;
    }
    this.getDetails();
  }

  getDetails() {
    this._propertyDetailsApi.getFinancialDetails(this.propertyId)
      .subscribe(res => {
        this.details = res || {};
        this.totalParts = (res && res.totalParts) || 0;
        this.financiaData = (res && res.projection) || {};
        this.computeFinancial();

        if (res && res.hasActualData) {
          this.options.push({
            name: 'Actuals',
            value: 2
          });
        }
      });
  }

  computeFinancial() {
    const {
      totalExpenseInvestorShare,
      performances
    } = this.financiaData;

    const totalPerformance = performances.reduce((acc, item) => {
      const temp = item.averageRate * item.occupancy * ( 45 - this.financialSlider ) * item.totalParts;
      return acc + temp;
    }, 0);

    this.totalPerformanceWithZeroUsage = performances.reduce((acc, item) => {
      const temp = item.averageRate * item.occupancy * ( 45 - 0 ) * item.totalParts;
      return acc + temp;
    }, 0);

    this.incomeShare = totalPerformance / this.totalParts;
    this.netIncome = this.incomeShare - totalExpenseInvestorShare;
    this.incomePercentage = 100 - ((this.financialSlider / 45) * 100);
    this.expensesPercentage = (totalExpenseInvestorShare / (this.totalPerformanceWithZeroUsage / this.totalParts)) * 100;
  }

  sliderChange(value) {
    this.financialSlider = value;
    this.computeFinancial();
  }

  setSelectedOption(value) {
    switch (value) {
      case 1:
        this.financiaData = (this.details && this.details.projection) || {};
        break;
      case 1:
        this.financiaData = (this.details && this.details.actual) || {};
        break;
      default:
    }
  }
}
