import { Component, OnInit, HostListener } from '@angular/core';

import { PropertyService, UsersService, TenantService } from '@shared/api';
import { Router, ActivatedRoute, Params, NavigationEnd } from '@angular/router';

import { environment } from '@env/environment';
import { PROPERTY_STATUS, STATUS_PARAMS, COUNTRY_PARAMS } from '@shared/constants/property-details';
import { Title } from '@angular/platform-browser';
import {
  GetMeNotifiedService,
  CurrentUserService,
  ValidationSharedService,
  VideoPlayerService,
  PropertyPageService,
  ConvertKitService
} from '@shared/services';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { BsModalService } from 'ngx-bootstrap';
import {
  ConfirmationComponent,
  ConfirmationProps
} from '@shared/components/modals/confirmation';
import { BrowserJsonp } from '@angular/http/src/backends/browser_jsonp';
import { STATUS_CODES } from 'http';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import { OrderPropertyModalComponent } from '@shared/components/modals/order-property-modal/order-property-modal.component';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { ModalService } from '@shared/services/modal/modal.service';
import * as moment from 'moment';
import {
  PropertyFilterModalComponent,
  SharePropertyModalComponent
} from '@modules/main-app/components/modals';
import { CONVERTKIT_TAGS } from '@shared/constants/convertkit';

@Component({
  selector: 'app-properties',
  templateUrl: './properties.component.html',
  styleUrls: ['./properties.component.scss']
})
export class PropertiesComponent implements OnInit {
  baseUrl = environment.baseUrl;
  filterCollapse: boolean = false;
  closeBanner: boolean = false;

  countrySortOptions = [];
  latestListingOptions = [
    { value: 'DateDesc', label: 'Latest Listing' },
    { value: 'DateAsc', label: 'Oldest Listing' },
    { value: 'PriceAsc', label: 'Lowest Price' },
    { value: 'PriceDesc', label: 'Highest Price' },
    // { value: 'RentalYieldAsc', label: 'Lowest Rental Yield' },
    // { value: 'RentalYieldDesc', label: 'Highest Rental Yield' }
  ];

  countrySortSelected = {};
  latestListingSelected = {};
  properties;
  propertiesLive;
  tempPropertyFunded;
  propertiesFunded;
  propertiesForMap;

  pageNumber = 1;
  pageSize = null;
  propertiesParams: { [key: string]: any } = {};
  mapClose;
  isForPropertyDetails = false;

  isLogin: boolean = false;
  isLoading: boolean = false;
  showFavourite: boolean = false;
  isFavoritesPage: boolean = false;
  isPropertiesEmpty: boolean = false;

  slideConfig = { 'slidesToShow': 1, 'slidesToScroll': 1, 'arrows': false };
  isSlidesTheCarousel: boolean = false;
  propertyStatus = 1;
  statuses: Array<any> = [];

  navigationSubscription;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth <= 767) {
      this.filterCollapse = true;
    }
    this.managePlaceholder();
  }

  constructor(
    private bsModalService: BsModalService,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private _getMeNotified: GetMeNotifiedService,
    private _propertyService: PropertyService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _users: UsersService,
    private _currentUser: CurrentUserService,
    private _modalService: ModalService,
    private _validationShared: ValidationSharedService,
    private _videoPlayer: VideoPlayerService,
    private _tenantSettings: TenantService,
    private _convertkit: ConvertKitService,
    private _propertyPageService: PropertyPageService
  ) {
    if (window.screen.width <= 767) {
      this.filterCollapse = true;
    }

    this._propertyPageService.update.subscribe(isFavorites => {
      this.isFavoritesPage = isFavorites;
      this.showFavourite = isFavorites;
      const params = {
        status: this.isFavoritesPage ? 'all' : 'upcoming',
        country: 'allcountries',
        sortby: 'DateDesc',
        fav: this.isFavoritesPage ? 1 : 0
      };
      this.setQueryParams(params);
      const tempStatus = STATUS_PARAMS.find(i => i.params === params.status);
      const tempCountry = COUNTRY_PARAMS.find(i => i.params === params.country);
      this.setFilters({
        status: tempStatus.value,
        country: tempCountry.value,
        sortby: params.sortby,
        fav: params.fav
      });
      this.updatePropertiesFilter(
        tempCountry.value,
        params.sortby,
        tempStatus.value
      );
    });
  }

  ngOnInit() {
    this.title.setTitle(`Partbnb - Properties`);
    this._urlQueryStringService.checkURLQueryString();
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.isLogin = (currentUser && currentUser.isLogin) || false;
    this.init();
    this.isSlidesTheCarousel = false;
  }

  init() {
    this.mapClose = true;
    this.countrySortOptions = [];
    this.countrySortOptions.push({
      countryId: 0,
      countryName: 'All Countries',
      stampDuty: 0.0,
      capitalGrowth: 0.0
    });
    this.countrySortSelected = this.countrySortOptions[0].countryId;
    this.latestListingSelected = this.latestListingOptions[0].value;
    // this.showFavourite = this._tenantSettings.getFavortiesSession();
    this.checkParams();
    this.getStatus();
    this.getCountry();
  }

  checkParams() {
    if (this.activeRoute.snapshot.queryParams && this.activeRoute.snapshot.queryParams.status) {
      const { status, country, sortby, fav} = this.activeRoute.snapshot.queryParams;
      const temp1 = STATUS_PARAMS.find(i => i.params === status);
      const temp2 = COUNTRY_PARAMS.find(i => i.params === country);
      this.setFilters({
        status: temp1.value,
        country: temp2.value,
        sortby,
        fav: this.isLogin ? fav : 0
      });
      this.updatePropertiesFilter(
        temp2.value,
        sortby,
        temp1.value
      );
      return;
    }
    const queryParams: Params = JSON.parse(localStorage.getItem('propertyParams'));
    this.setQueryParams(queryParams);
    const tempStatus = STATUS_PARAMS.find(i => i.params === queryParams.status);
    const tempCountry = COUNTRY_PARAMS.find(i => i.params === queryParams.country);
    this.setFilters({
      status: tempStatus.value,
      country: tempCountry.value,
      sortby: queryParams.sortby,
      fav: queryParams.fav
    });
    this.updatePropertiesFilter(
      tempCountry.value,
      queryParams.sortby,
      tempStatus.value
    );
  }

  setQueryParams(queryParams) {
    this.router.navigate(
      [],
      {
        relativeTo: this.activeRoute,
        queryParams: {
          ...queryParams
        },
        queryParamsHandling: 'merge',
      }
    );
  }

  setFilters({status, country, sortby, fav}) {
    this.showFavourite = !!parseInt(fav, 10);
    this.setFavoritesUI();
    this.propertyStatus = status;
    this.countrySortSelected = country;
    this.latestListingSelected = sortby;
  }

  setFavoritesUI() {
    if (this.showFavourite) {
      this.isFavoritesPage = true;
      this.propertyStatus = 0;
      this.countrySortSelected = this.countrySortOptions[0].countryId;
      this.latestListingSelected = this.latestListingOptions[0].value;
      this.updatePropertiesFilter(
        this.countrySortSelected,
        this.latestListingSelected,
        this.propertyStatus
      );
      return;
    }
  }

  setUIBack() {
    this.isFavoritesPage = false;
    this.showFavourite = false;
    this.updatePropertiesFilter(
      this.countrySortSelected,
      this.latestListingSelected,
      this.propertyStatus
    );
  }

  getCountry() {
    this._propertyService.getCountry().subscribe(data => {
      this.countrySortOptions = this.countrySortOptions.concat(data);
    });
  }

  getStatus() {
    this._propertyService.getPropertyFilter().subscribe(res => {
      const data = res || [];
      this.statuses = [
        {
          propertyStatusFilterID: 0,
          propertyStatusFilterName: 'All Properties'
        },
        ...data
      ];
    });
  }

  getProperties(params) {
    this.isLoading = true;
    this._propertyService.getProperties(params).subscribe(data => {
      this.isLoading = false;
      if (!data || !data.length) {
        this.isPropertiesEmpty = true;
        this.getDefaultLocations();
        return;
      }
      this.isPropertiesEmpty = false;
      this.properties = data.map(item => {
        item.overviewData = item.overview.split( '\\n' );
        return item;
      });
      this.propertiesForMap = data;
      // this.propertiesLive = this.properties.filter(
      //   i => i.propertyListingTypeId === PROPERTY_STATUS.LIVE
      // );
      // this.tempPropertyFunded = this.properties.filter(
      //   i => i.propertyListingTypeId !== PROPERTY_STATUS.LIVE
      // );
      // this.propertiesForMap = this.properties.filter(
      //   i => i.propertyListingTypeId !== PROPERTY_STATUS.INACTIVE
      // );
      // this.managePlaceholder();
    });
  }

  getDefaultLocations() {
    const settings = this._tenantSettings.getSettings();
    const latitude = settings.find(i => i.tenantSettingId === 1);
    const longitude = settings.find(i => i.tenantSettingId === 2);
    const mapObj = {
      locationLatitude: latitude.settingValue,
      locationLongitude: longitude.settingValue
    };

    this.propertiesForMap = [mapObj];
  }

  managePlaceholder() {
    const length = this.tempPropertyFunded.length;
    if (!length) {
      this.propertiesFunded = [];
      return;
    }
    this.propertiesFunded = this.tempPropertyFunded.filter(
      i => i.propertyListingTypeId !== PROPERTY_STATUS.TEMPHOLDER
    );
    // console.log('propertiesFunded', this.propertiesFunded);
    // if (window.innerWidth <= 991 && window.innerWidth > 767) {
    //   // Tablet
    //   const minLenght = 2;
    //   if (length >= minLenght) {
    //     return;
    //   }
    //   const additionalPlaceholder = minLenght - length;
    //   this.addPlaceholder(additionalPlaceholder);
    // } else if (window.innerWidth > 991) {
    //   // Desktop
    //   const minLenght = 3;
    //   if (length >= minLenght) {
    //     return;
    //   }
    //   const additionalPlaceholder = minLenght - length;
    //   this.addPlaceholder(additionalPlaceholder);
    // }
  }

  addPlaceholder(length) {
    for (let i = 1; i <= length; i++) {
      const placeholder = {
        propertyListingTypeId: PROPERTY_STATUS.TEMPHOLDER
      };
      this.propertiesFunded.push(placeholder);
    }
  }

  checkProperties() {
    if (!this.properties || !this.properties.length) {
      return;
    }
    const currentUser = JSON.parse(localStorage.getItem('currentUser')) || {};
    this.properties.map(obj => {
      if (
        obj.propertyListingTypeId !== PROPERTY_STATUS.COMING_SOON &&
        currentUser.isLogin
      ) {
        this._users
          .checkUserForPreOrder(currentUser.username, obj.propertyId)
          .subscribe(res => {
            obj.isNotAllow = !(res && res.canPreOrder);
          });
      } else {
        obj.isNotAllow = true;
      }
      return obj;
    });
  }

  propertyFavoriteFilter() {
    if (!this.isLogin) {
      this.navigateTo('login');
      return;
    }
    this.showFavourite = !this.showFavourite;
    if (!this.isFavoritesPage) {
      this._propertyPageService.setNavigatorPage(true);
      return;
    }
    this.updatePropertiesFilter(this.countrySortSelected, this.latestListingSelected, this.propertyStatus);
  }

  updatePropertiesFilter(countryId, sortingMode, statusFilter) {
    this.propertiesParams = {
      pageNumber: this.pageNumber,
      pageSize: this.pageSize,
      sortingMode,
      statusFilter,
      favorite: this.showFavourite
    };
    if (!!countryId) {
      this.propertiesParams.countryId = countryId;
    }
    const queryParams = {
      status: statusFilter,
      country: countryId ? countryId : 0,
      sortby: sortingMode,
      fav: this.showFavourite ? 1 : 0
    };
    this._tenantSettings.setPropertiesParams(queryParams);
    const tempStatus = STATUS_PARAMS.find(i => i.value === queryParams.status);
    const tempCountry = COUNTRY_PARAMS.find(i => i.value === queryParams.country);
    this.setQueryParams({...queryParams, status: tempStatus.params, country: tempCountry.params});
    this.getProperties(this.propertiesParams);
  }

  getMeNotified() {
    this._getMeNotified.toggle();
    // this._getMeNotified.setPreOrderStatus(true);
    this._getMeNotified.setPreOrderStatus(false);
  }

  addPropertyToFavorite(property) {
    if (!this.isLogin) {
      this.navigateTo('login');
      return;
    }
    if (!property.favorite) {
      this._convertkit.subscribeConvertkitTag(`${CONVERTKIT_TAGS.FAVORITES}${property.propertyId}`)
        .subscribe();
    } else {
      this._convertkit.unsubscribeConvertkitTag(`${CONVERTKIT_TAGS.FAVORITES}${property.propertyId}`)
        .subscribe();
    }
    this._propertyService.setPropertyToFavorite(property.propertyId, { value: !property.favorite }).subscribe(res => {
      this.updatePropertiesFilter(this.countrySortSelected, this.latestListingSelected, this.propertyStatus);
    });
  }

  viewDetails(id, invalid = false) {
    if (invalid || this.isSlidesTheCarousel) {
      this.navigateToFromDetails('login', id);
      return;
    }
    this.router.navigate([`/property/${id}`]);
    this.isSlidesTheCarousel = false;
  }

  preOrder(item) {
    const {
      verificationStatus,
      isAccreditedInvestor
    } = this._currentUser.getUserInfo();
    if (!isAccreditedInvestor) {
      this._validationShared.openWarningForNonAccredited();
      return;
    }
    if (verificationStatus === VERIFICATION_STATUS.APPROVED) {
      if (item.propertyListingTypeId === PROPERTY_STATUS.PRE_ORDER) {
        this.router.navigate([`/preorder/${item.propertyId}`]);
        return;
      }
      const details = {
        ...item
      };
      this._modalService.modalInstance(OrderPropertyModalComponent, {
        initialState: { details },
        animated: false,
        class: 'centered-modal'
      });
    } else {
      const props: ConfirmationProps = {
        status: 'warning',
        title: 'You need to be verified as an Accredited Investor',
        desc:
          'We need to verify your status as an Accredited Investor before you can proceed.',
        isButtonDisabled:
          verificationStatus === VERIFICATION_STATUS.PENDING || false,
        buttonLabel:
          verificationStatus === VERIFICATION_STATUS.PENDING
            ? 'Verification Pending'
            : 'Start Verification'
      };
      const modalRef = this._modalService.modalInstance(ConfirmationComponent, {
        initialState: { props },
        animated: false,
        class: 'confirmation'
      });
      (<ConfirmationComponent>modalRef).onClose.subscribe(result => {
        if (result && isAccreditedInvestor) {
          this.openInvestorVerify();
        }
      });
    }
  }

  openInvestorVerify() {
    this._modalService.modalInstance(InvestorVerificationComponent, {
      ignoreBackdropClick: true,
      class: 'modal-lg'
    });
  }

  toggleFilter() {
    this.filterCollapse = !this.filterCollapse;
  }

  isPropertyComingSoon(status) {
    return (
      status === PROPERTY_STATUS.COMING_SOON ||
      status === PROPERTY_STATUS.PLACEHOLDER
    );
  }

  isPropertyPlaceHolder(status) {
    return status === PROPERTY_STATUS.PLACEHOLDER;
  }

  isPropertyTemp(status) {
    return status === PROPERTY_STATUS.TEMPHOLDER;
  }

  isPropertyPreOrder(status) {
    return status === PROPERTY_STATUS.PRE_ORDER;
  }

  navigateTo(url) {
    this.router.navigate([`/${url}`], {
      queryParams: {
        // returnUrl: this.router.url.toString()
        returnUrl: `/properties`
      }
    });
  }

  navigateToFromDetails(url, propertyId) {
    this.router.navigate([`/${url}`], {
      queryParams: {
        returnUrl: `/property/${propertyId}`
      }
    });
  }

  openMinimumFindingModal(status: boolean) {
    if (status) {
      const props: ConfirmationProps = {
        status: 'success',
        title: 'Minimum Funding Reached',
        desc: `This property has reached the minimum
        funding target needed in order to guarantee that
        Partbnb will proceed with the purchase
        / settlement / renovation of the property.`,
        buttonLabel: 'Okay, got it'
      };
      this._modalService.modalInstance(ConfirmationComponent, {
        initialState: { props },
        animated: false,
        class: 'confirmation'
      });
    }
  }

  propertyIsLive(status) {
    return status === PROPERTY_STATUS.LIVE;
  }

  properyButtonLabel(status) {
    switch (status) {
      case PROPERTY_STATUS.PRE_ORDER:
        return 'Pre-Order';
      case PROPERTY_STATUS.LIVE:
        return 'Buy Parts';
      case PROPERTY_STATUS.COMING_SOON:
        return 'Pre-Order';
      default:
        return 'Pre-Order';
    }
  }

  getCardClass(status) {
    switch (status) {
      case PROPERTY_STATUS.PLACEHOLDER:
        return 'placeholder';
      case PROPERTY_STATUS.TEMPHOLDER:
        return 'tempholder placeholder';
      default:
        return '';
    }
  }

  markMessage(type: string) {
    const props: ConfirmationProps = {
      status: 'question',
      title: type === 'resale' ? 'Parts for Resale' : 'Part Price',
      desc:
        type === 'resale'
          ? `These are parts which are available for re-sale by another investor,
        or held by Partbnb. In some cases Partbnb will help to fund the completion
        of a property purchase, and may elect to resell its parts.
        Investors can list their parts for sale after 6 months of ownership.`
          : `The part price is calculated by taking the total value of the property
        project & dividing it into 10,000 parts (shares). /n
        Example: Value of $300,000 / 10,000 = $30 per part`
    };
    const modalRef = this._modalService.modalInstance(ConfirmationComponent, {
      initialState: { props },
      animated: false,
      class: 'confirmation'
    });
  }

  setPropertyImage(path) {
    return {
      'background-image': `url(${path})`
    };
  }

  afterChange(e) {
    this.isSlidesTheCarousel = false;
  }

  beforeChange(e) {
    this.isSlidesTheCarousel = true;
  }

  propertyInfoDate(propertyData) {
    switch (propertyData.propertyStatusId) {
      case 1:
        return `Opens ${moment(propertyData.openDate).format('DD MMMM')}`;
      case 2:
        return `Closes ${moment(propertyData.closingDate).format('DD MMMM')}`;
      case 3:
        return `${propertyData.partsForResaleCount} Parts Available`;
      case 4:
        return `No Parts Available`;
      default:
    }
  }

  chartObject(statusID, owned) {
    return {
      statusID,
      owned
    };
  }

  openMobileFilter() {
    const props = {
      countries: this.countrySortOptions,
      sorting: this.latestListingOptions,
      countrySortSelected: this.countrySortSelected,
      latestListingSelected: this.latestListingSelected,
      mapClose: this.mapClose,
      showFavourite: this.showFavourite
    };

    const modalRef = this._modalService.modalInstance(PropertyFilterModalComponent, {
      initialState: { props },
      animated: false,
      class: 'filter-modal'
    });

    (<PropertyFilterModalComponent>modalRef).onClose.subscribe(result => {
      if (result) {
        this.countrySortSelected = result.countrySortSelected;
        this.latestListingSelected = result.latestListingSelected;
        this.mapClose = result.mapClose;
        this.showFavourite = result.showFavourite;
        this.updatePropertiesFilter(this.countrySortSelected, this.latestListingSelected, this.propertyStatus);
      }
    });
  }

  playVideo(videoLink, title) {
    if (!videoLink) {
      return;
    }
    this._videoPlayer.toggle();
    this._videoPlayer.setVideo(videoLink);
    this._videoPlayer.setTitle(title);
  }

  shareProperty(propertyId) {
    if (!this.isLogin) {
      this.navigateTo('login');
      return;
    }

    const modalRef = this._modalService.modalInstance(SharePropertyModalComponent, {
      initialState: { propertyId },
      animated: false,
      class: 'filter-modal'
    });

    // (<PropertyFilterModalComponent>modalRef).onClose.subscribe(result => {
    //   if (result) {
    //     this.countrySortSelected = result.countrySortSelected;
    //     this.latestListingSelected = result.latestListingSelected;
    //     this.mapClose = result.mapClose;
    //     this.showFavourite = result.showFavourite;
    //     this.updatePropertiesFilter(this.countrySortSelected, this.latestListingSelected, this.propertyStatus);
    //   }
    // });
  }
}
