import { Component, OnInit, HostListener, Input } from '@angular/core';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import * as moment from 'moment';
import { VideoPlayerService } from '@shared/services';

@Component( {
  selector: 'open-homes-content',
  templateUrl: './open-homes-content.component.html',
  styleUrls: ['./open-homes-content.component.scss']
} )
export class OpenHomesContentComponent implements OnInit {
  @Input( 'propertyId' ) id: number;
  details;
  baseClassName;

  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
      this.baseClassName = this._viewportService.sizeClassName();
    }, 250 );
  }

  constructor (
    private _propertyDetailsService: PropertyDetailsService,
    private _viewportService: ViewportService,
    private _videoPlayer: VideoPlayerService
  ) { }

  ngOnInit () {
    this._viewportService.checkViewSizes();
    this.baseClassName = this._viewportService.sizeClassName();
    this.getOpenHomesDetails();
  }

  getOpenHomesDetails () {
    this._propertyDetailsService
      .getOpenHomesDetails( this.id, { date: moment().format() } )
      .subscribe( res => {
        this.details = res;
      } );
  }

  getDay ( date ) {
    return moment( date ).format( 'DD' );
  }

  getMonth ( date ) {
    return moment( date ).format( 'MMMM' );
  }

  getTime ( date ) {
    return moment( date ).format( 'hh:mma' );
  }

  getButtonText ( isLive, isReg ) {
    return isLive ? 'Join Now!' : isReg ? ' Registered' : 'Register Interest';
  }

  setOpenHomeAction ( openHome ) {
    if ( openHome.currentlyLive ) {
      window.open( openHome.url, '_blank' );
    } else {
      if ( !openHome.registered ) {
        this.setOpenHomesRegisterStatus( true, openHome.propertyOpenHomeID );
      }
    }
  }

  setOpenHomesRegisterStatus ( isReg, openHomeId ) {
    this._propertyDetailsService.setOpenHomesRegisterStatus( this.id, { register: isReg }, openHomeId ).subscribe( () => {
      this.getOpenHomesDetails();
    } );
  }


  getVideoThumnail ( url ) {
    var youtubeId = url.match( /(?:https?:\/{2})?(?:w{3}\.)?youtu(?:be)?\.(?:com|be)(?:\/watch\?v=|\/)([^\s&]+)/ ) || [];
    return `https://i.ytimg.com/vi/${youtubeId[1]}/mqdefault.jpg`;
  };

  playVideo ( videoLink, title ) {
    if ( !videoLink ) {
      return;
    }
    this._videoPlayer.toggle();
    this._videoPlayer.setVideo( videoLink );
    this._videoPlayer.setTitle( title );
  }

}
