import { Pipe, PipeTransform } from '@angular/core';
import * as Fuse from 'fuse.js';

@Pipe({
  name: 'filterFaq',
})
export class FilterFaq implements PipeTransform {
  transform(items: any[], searchInput: string, keys:any[], threshold = 0.0): any[] {
    const options: object = {
      shouldSort: true,
      threshold: threshold,
      location: 0,
      distance: 100,
      maxPatternLength: 32,
      minMatchCharLength: 1,
      keys: keys 
    };

    const fuse: any = new Fuse(items, options);

    return searchInput ? fuse.search(searchInput.toLowerCase()) : [];
  }
}