import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ViewChildren, ComponentFactoryResolver, QueryList, Input } from '@angular/core';
import { BannerComponent } from '@modules/main-app/sections/banner/banner.component';
import { TitledOwnershipComponent } from '@modules/main-app/sections/titled-ownership/titled-ownership.component';
import { HowItWorksComponent } from '@modules/main-app/sections/how-it-works/how-it-works.component';
import { SharedOwnershipComponent } from '@modules/main-app/sections/shared-ownership/shared-ownership.component';
import { FeaturesAndBenefitsComponent } from '@modules/main-app/sections/features-and-benefits/features-and-benefits.component';
import { OwnersLoveUsComponent } from '@modules/main-app/sections/owners-love-us/owners-love-us.component';
import { FrequentlyQuestionsComponent } from '@modules/main-app/sections/frequently-questions/frequently-questions.component';
import { WhatsAvailableComponent } from '@modules/main-app/sections/whats-available/whats-available.component';
import { NeedHelpComponent } from '@modules/main-app/sections/need-help/need-help.component';
import { OurPartnersComponent } from '@modules/main-app/sections/our-partners/our-partners.component';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html', 
  styleUrls: ['./home.component.scss'],
  entryComponents: [ 
    BannerComponent, 
    TitledOwnershipComponent, 
    HowItWorksComponent, 
    SharedOwnershipComponent, 
    FeaturesAndBenefitsComponent, 
    OwnersLoveUsComponent, 
    FrequentlyQuestionsComponent, 
    WhatsAvailableComponent,
    NeedHelpComponent, 
    OurPartnersComponent ]
})
export class HomeComponent implements OnInit {
  
  constructor(private title: Title, private componentFactoryResolver: ComponentFactoryResolver) {}

  sections =[]

  @ViewChildren('target', {read: ViewContainerRef}) container: QueryList<ViewContainerRef>;
  ngOnInit() {
    this.title.setTitle(`Partbnb - Home`);
    this.sections.push({component: BannerComponent, model:{} });
    this.sections.push({component: TitledOwnershipComponent, model:{} });
    this.sections.push({component: HowItWorksComponent, model:{} });
    this.sections.push({component: SharedOwnershipComponent, model:{} });
    this.sections.push({component: FeaturesAndBenefitsComponent, model:{} });
    this.sections.push({component: OwnersLoveUsComponent, model:{} });
    this.sections.push({component: FrequentlyQuestionsComponent, model:{
      items:[
          {
            title: 'Is your part ownership really a timeshare?',
            description: 'No, our goal with Partbnb is to help you and other people who love the Caribbean obtain genuine, fair and transparent shared ownership of standalone vacation homes. We believe the way to achieve that is that you and our other owners should share in the title, value increases, income, expenses, and major decisions affecting the property, while all owners are free to sell their partial ownership whenever they choose. '
          },
          {
            title: 'What are the fees/costs?',
            description: 'All fees are included in the breakdown of the price-per-part on the property. There are usual fees/costs related to purchasing a property, including setup fees, stamp duties, company setup, and legal fees. There is also a yearly maintenance fee payable for Partbnb to operate the ownership structure, manage the property, and manage short-term rentals. We believe in total transparency, and you can view details of all fees/costs <a href="/fees-and-costs">here</a>.'
          },
          {
            title: 'What does Partbnb do?',
            description: 'When you purchase the property, you agree that were appointed as the property ownership manager on behalf of all the owners. Our initial job is to locate suitable properties, bring together potential owners, and ensure a smooth purchase process for all. Then we ensure the property is setup and operated as a carefree vacation home. Our team takes care of all maintenance, bookings, short-term rentals, owner meetings, updates, reporting, guest assistance, financials etc. We act as a convenient intermediary between all owners, including operating the platform to assist you and other owners in all aspects of ongoing partial ownership.'
          },
          {
            title: 'Who is eligible to buy?',
            description: 'There are no citizenship restrictions on eligibility. To make a purchase, you must be able to prove that you have the funds to facilitate the settlement of your part. In rare cases, certain individuals may not receive approval from the local government to purchase a property due to criminal convictions or other reasons for ineligibility. Please contact our team if you are unsure about your eligibility.'
          },
          {
            title: 'When can I stay at the home?',
            description: 'Each part owner receives 45 days a year for each part they own. A dedicated booking calendar is used to secure and record owner bookings. In the event of a conflict, our fair prioritization system is utilized. Short-term rentals are made available on unused nights. The rentals are offered as a last resort so owners get the first pick of available times. View full details about the booking system <a href="/features-and-benefits">here</a>.'
          },
          {
            title: 'How do I sell the home?',
            description: 'There are two ways to sell your partial ownership. 1) You can offer to sell your part or parts on our resale page. 2) If the majority of owners wish to sell the property and vote to do so, Partbnb will arrange for a valuation and sale of the property on the open market.'
          }
      ]
    } });
    this.sections.push({component: WhatsAvailableComponent, model:{} });
    this.sections.push({component: NeedHelpComponent, model:{} });
    this.sections.push({component: OurPartnersComponent, model:{} });
  }

  timeoutVal;
  ngAfterViewInit(){
    this.timeoutVal = setTimeout(() => {
      this.loadComponents();
    });
  }

 
  loadComponents(){
    this.container.toArray().map((viewContainerRef, i) => {
      let componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.sections[i].component);
      let instance: any = viewContainerRef.createComponent(componentFactory).instance;
      instance.model = this.sections[i].model;
    });
  }
  

  ngOnDestroy() {
    clearTimeout(this.timeoutVal);
  }
}
