import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { PropertyDetailsService } from '@shared/api';
import { SlickCarouselComponent } from 'ngx-slick-carousel';
import { PhotoGalleryService, PhotoCarouselService } from '@shared/services';

@Component({
  selector: 'location-details-content',
  templateUrl: './location-details-content.component.html',
  styleUrls: ['./location-details-content.component.scss']
})
export class LocationDetailsContentComponent implements OnInit {
  @ViewChild('slickSay') slickSay: SlickCarouselComponent;
  @Input() propertyId: number;
  details: any;

  locationData: Array<any> = [];
  suburbPerformance;
  capitalGrowths;
  capitalGrowthsObj;
  propertyLocationReviews;
  propertyNearbyAttractions;

  slideConfig = { 'slidesToShow': 1, 'slidesToScroll': 1, 'arrows': false };
  slideActive: number = 0;
  isSlideClickNext: boolean = false;
  attractionsSetLimit: boolean = true;

  constructor(
    private _propertyDetailsApi: PropertyDetailsService,
    private _photoGalleryService: PhotoGalleryService,
    private _photoCarouselService: PhotoCarouselService
  ) { }

  ngOnInit() {
    if (!this.propertyId) {
      return;
    }
    this.getDetails();
  }

  getDetails() {
    this._propertyDetailsApi.getLocationDetails(this.propertyId)
      .subscribe(res => {
        this.details = res;
        this.suburbPerformance = (res && res.suburbPerformance) || [];
        this.capitalGrowths = (res && res.capitalGrowths) || [];
        this.capitalGrowthsObj = (res && res.capitalGrowths &&
          res.capitalGrowths.length && res.capitalGrowths[0]) || {};
        this.propertyLocationReviews = (res && res.propertyLocationReviews) || [];
        this.propertyNearbyAttractions = (res && res.propertyNearbyAttractions) || [];
        this.setLocation();
      });
  }

  setLocation() {
    this.locationData = [];
    const marker: { [key: string]: any } = {};

    marker.locationLatitude = this.details.locationLatitude;
    marker.locationLongitude = this.details.locationLongitude;
    marker.propertyID = this.details.propertyID;
    // marker.propertyAddress = this.details.propertyAddress;
    // marker.propertyName = this.details.propertyName;

    this.locationData.push(marker);
  }

  showImageGallery(id = 0) {
    const newPhotosList = this.propertyNearbyAttractions.map(i => {
      i.filePath = i.photo;
      return i;
    });
    this._photoGalleryService.toggle();
    this._photoGalleryService.setImages(newPhotosList);
    this._photoGalleryService.setShowThumbnails(true);
    this._photoGalleryService.selectedImage(id);
    this._photoCarouselService.setImageIndex(id);
  }

  slickInit(e) {
    e.slick.setPosition();
  }

  nextReview() {
    this.slickSay.slickNext();
    this.isSlideClickNext = true;
  }

  prevReview() {
    this.slickSay.slickPrev();
    this.isSlideClickNext = false;
  }

  afterChange() {
    if (this.isSlideClickNext) {
      if (this.slideActive === this.propertyLocationReviews.length - 1) {
        this.slideActive = 0;
        return;
      }
      this.slideActive++;
    } else {
      if (this.slideActive === 0) {
        this.slideActive = this.propertyLocationReviews.length - 1;
        return;
      }
      this.slideActive--;
    }
  }
}
