import {
  Component,
  OnInit,
  Output,
  AfterViewInit,
  EventEmitter,
  HostListener,
  ViewChild,
  ChangeDetectorRef
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '@env/environment';

import { TABS } from '@shared/constants/property-details';

import { PropertyService, UsersService } from '@shared/api';
import { Property } from '@shared/api/property/property.model';
import {
  PropertySummary,
  FinancialOverview,
  FinancialIncome,
  FinancialCapitalDetails,
  FinancialCapitalGrowth,
  PropertyLocation,
  FloorPlanLayout,
  Timeline,
  Video,
  FinancialExpense,
  FinancialSummary
} from '@shared/api/property-details/property-details.model';
import { PhotoGalleryService } from '@shared/services/photo-gallery/photo-gallery.service';
import { PhotoCarouselService } from '@shared/services/photo-carousel/photo-carousel.service';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import {
  PropertyImages,
  PropertyAssetTypes
} from '@shared/api/property-details/property-details.model';
import { NgbCarousel } from '@ng-bootstrap/ng-bootstrap';
import { PROPERTY_STATUS } from '@shared/constants/property-details';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { BsModalService } from 'ngx-bootstrap';
import {
  ConfirmationComponent,
  ConfirmationProps
} from '@shared/components/modals/confirmation';
import { CurrentUserService, ValidationSharedService, VideoPlayerService, ConvertKitService } from '@shared/services';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { OrderPropertyModalComponent } from '@shared/components/modals/order-property-modal/order-property-modal.component';
import { ModalService } from '@shared/services/modal/modal.service';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { HEIGHT } from '@shared/constants/property-carousel.ts';
import * as moment from 'moment';
import { NEW_PROPERTY_STATUS } from '@shared/constants/property-details';
import { SharePropertyModalComponent, RegisterInterestModalComponent } from '@modules/main-app/components/modals';
import { CONVERTKIT_TAGS } from '@shared/constants/convertkit';

const PROPERTY_ASSETS = {
  PHOTOS: 1,
  FLOORPLAN: 2,
  WALKTHROUGH_VIDEO: 3,
  NEIGHBOURHOOD: 4,
  PPM: 5
};


@Component( {
  selector: 'app-property-details',
  templateUrl: './property-details.component.html',
  styleUrls: ['./property-details.component.scss']
} )
export class PropertyDetailsComponent implements OnInit, AfterViewInit {
  closeBanner: boolean = false;
  activeTabs;

  images: string[];
  propertyId: number;
  propertyDetails: Property;
  summaryDetails: PropertySummary;
  summaryPrivateDocs: PropertyImages;
  testInput: 'string';
  propertyImages: PropertyImages;
  propertyAssetTypes: PropertyAssetTypes[];
  baseUrl = environment.baseUrl;
  propertyAddress;
  propertyLocation: PropertyLocation;
  floorPlanLayout: FloorPlanLayout;
  video: Video;
  isPreOrderNotAllow: boolean = false;
  showActuals: boolean;
  showProjected: boolean;

  // Financial Tabs
  financialOverview: FinancialOverview;
  financiaIncome: FinancialIncome;
  financialExpense: FinancialExpense;
  financialCapitalDetails: FinancialCapitalDetails[];
  financialCapitalGrowth: FinancialCapitalGrowth[];
  timelines: Timeline[];
  financialSummary: FinancialSummary;
  financialFilters;

  isLogin: boolean = false;
  hideBanner: boolean = true;
  customOptions: OwlOptions = {
    navSpeed: 700,
    center: true,
    nav: false,
    mouseDrag: false,
    dots: false,
    loop: true,
    autoWidth: true,
    margin: 3,
    lazyLoadEager: 3
  };
  loadImgCounter = 0;
  propertyImageLength = 0;
  carouselHeight = HEIGHT.DESKTOP;

  isDetailsReady: boolean = false;
  slideConfig = { 'slidesToShow': 1, 'slidesToScroll': 1 };
  isSlidesTheCarousel: boolean = false;
  isOverviewCollapse: boolean = true;
  NEW_PROPERTY_STATUS = NEW_PROPERTY_STATUS;

  @Output()
  openImageGallery = new EventEmitter();

  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
    }, 500 );
  }

  constructor (
    private router: Router,
    private activeRoute: ActivatedRoute,
    private bsModalService: BsModalService,
    private _propertyService: PropertyService,
    private _photoGalleryService: PhotoGalleryService,
    private _propertyDetailsService: PropertyDetailsService,
    private changeDetectorRef: ChangeDetectorRef,
    private _photoCarouselService: PhotoCarouselService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _users: UsersService,
    private _currentUser: CurrentUserService,
    private _modalService: ModalService,
    private _viewportService: ViewportService,
    private _validationShared: ValidationSharedService,
    private _convertkit: ConvertKitService,
    private _videoPlayer: VideoPlayerService
  ) {
  }

  ngOnInit () {
    this.title.setTitle( `Partbnb - Property Details` );
    if ( !this.activeRoute.snapshot.params.hasOwnProperty( 'id' ) ) {
      this.router.navigate( ['/properties'] );
    } else {
      const currentUser = JSON.parse( localStorage.getItem( 'currentUser' ) );
      this.isLogin = ( currentUser && currentUser.isLogin ) || false;

      if ( !this.isLogin ) {
        this.router.navigate(['/login'], {
          queryParams: {
            returnUrl: this.router.url.toString()
          }
        });
      }

      this.propertyId = this.activeRoute.snapshot.params.id || 0;
      this.getPropertyImages( { propertyId: this.propertyId, propertyAssetTypeId: PROPERTY_ASSETS.PHOTOS } );
      this.getDetails( this.propertyId );
    }
  }

  ngAfterViewInit () {
  }

  getDetails ( propertyId ) {
    this.isDetailsReady = false;
    this._propertyService.getPropertyDetails( propertyId ).subscribe(
      res => {
        this.propertyDetails = res && res.data;
        this.isDetailsReady = true;
      },
      () => {
        this.router.navigate( ['/properties'] );
      }
    );
  }

  getPropertyImages ( detailIds ) {
    this._propertyDetailsService
      .getPropertyImages( detailIds )
      .subscribe( data => {
        this.propertyImages = data;
        this.propertyImageLength = ( <any>this.propertyImages ).length;
      } );
  }

  addPropertyToFavorite () {
    if (!this.propertyDetails.favorite) {
      this._convertkit.subscribeConvertkitTag(`${CONVERTKIT_TAGS.FAVORITES}${this.propertyId}`)
        .subscribe();
    } else {
      this._convertkit.unsubscribeConvertkitTag(`${CONVERTKIT_TAGS.FAVORITES}${this.propertyId}`)
        .subscribe();
    }
    this._propertyService.setPropertyToFavorite(
      this.propertyDetails.propertyId,
      { value: !this.propertyDetails.favorite }
    ).subscribe( res => {
      this.propertyDetails.favorite = !this.propertyDetails.favorite;
    } );
  }

  showImageGallery ( id = 0 ) {
    this._photoGalleryService.toggle();
    this._photoGalleryService.setImages( this.propertyImages );
    this._photoGalleryService.setShowThumbnails( true );
    this._photoGalleryService.selectedImage( id );
    this._photoCarouselService.setImageIndex( id );
  }

  setButtonDisable ( statusFilterId ) {
    switch ( statusFilterId ) {
      case 1:
        return false;
      case 2:
        return false;
      case 3:
        return false;
      case 4:
        return true;
      default:
        return true;
    }
  }

  setButtonLabel ( details ) {
    switch ( details.statusFilterId ) {
      case 1:
        return details.interested ? 'Registered' : 'Register Interest';
      case 2:
        return 'Buy Part';
      case 3:
        return 'Sell Part';
      case 4:
        return 'Unavailable';
      default:
        return 'Unavailable';
    }
  }

  setPropertyImage ( path ) {
    return {
      'background-image': `url(${path})`
    };
  }

  propertyInfoDate ( propertyData ) {
    switch ( propertyData.propertyStatusId ) {
      case 1:
        return `Opens ${moment( propertyData.openDate ).format( 'DD MMMM' )}`;
      case 2:
        return `Closes ${moment( propertyData.closingDate ).format( 'DD MMMM' )}`;
      case 3:
        return `${propertyData.partsForResaleCount} Parts Available`;
      case 4:
        return `No Parts Available`;
      default:
    }
  }

  chartObject ( property ) {
    return {
      statusID: property.propertyStatusId,
      owned: property.totalPartsOwned
    };
  }

  playVideo ( videoLink, title ) {
    if ( !videoLink ) {
      return;
    }
    this._videoPlayer.toggle();
    this._videoPlayer.setVideo( videoLink );
    this._videoPlayer.setTitle( title );
  }

  shareProperty () {
    const modalRef = this._modalService.modalInstance( SharePropertyModalComponent, {
      initialState: { propertyId: this.propertyId },
      animated: false,
      class: 'filter-modal'
    } );

    // (<PropertyFilterModalComponent>modalRef).onClose.subscribe(result => {
    //   if (result) {
    //     this.countrySortSelected = result.countrySortSelected;
    //     this.latestListingSelected = result.latestListingSelected;
    //     this.mapClose = result.mapClose;
    //     this.showFavourite = result.showFavourite;
    //     this.updatePropertiesFilter(this.countrySortSelected, this.latestListingSelected, this.propertyStatus);
    //   }
    // });
  }

  mainButton(details) {
    if (details.propertyStatusId === 1 && !details.interested) {
      this.registerInterest();
    }
  }

  registerInterest() {
    const modalRef = this._modalService.modalInstance( RegisterInterestModalComponent, {
      initialState: { propertyId: this.propertyId },
      animated: false,
      class: 'register-interest-modal'
    });

    (<RegisterInterestModalComponent>modalRef).onClose.subscribe(result => {
      if (result) {
        this._convertkit.subscribeConvertkitTag(`${CONVERTKIT_TAGS.INTEREST}${this.propertyId}`)
          .subscribe();
        this.propertyDetails = {
          ...this.propertyDetails,
          interested: true
        };
      }
    });
  }

  divideOverview ( overview ) {
    return overview.split( '\\n' );
  }

  afterChange ( e ) {
    this.isSlidesTheCarousel = false;
  }

  beforeChange ( e ) {
    this.isSlidesTheCarousel = true;
  }
}
