import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-owners-agreement-section',
  templateUrl: './owners-agreement.component.html',
  styleUrls: ['./owners-agreement.component.scss']
})
export class OwnersAgreementComponent implements OnInit {

  constructor() { }
  @Input() model: any;
  ngOnInit() {
    if(this.model.items)
      this.selectedItem = this.model.items[1];
  }


  selectedItem;
  select(item){
    this.selectedItem = item;
  }
}
