import { Component, OnInit, HostListener, Input } from '@angular/core';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import * as moment from 'moment';
import { CHARTTYPES } from '@shared/constants/chart-types';
import { CAPITAL_BREAKDOWN, CAPITAL_BREAKDOWN_COLOR_SEQUENCE } from '@shared/constants/property-details';

@Component( {
  selector: 'offer-details-content',
  templateUrl: './offer-details-content.component.html',
  styleUrls: ['./offer-details-content.component.scss']
} )
export class OfferDetailsContentComponent implements OnInit {
  @Input( 'propertyId' ) id: number;
  details;
  baseClassName;
  CHARTTYPES = CHARTTYPES;
  CAPITAL_BREAKDOWN = CAPITAL_BREAKDOWN;
  chartValues = [];

  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
      this.baseClassName = this._viewportService.sizeClassName();
    }, 250 );
  }

  constructor (
    private _propertyDetailsService: PropertyDetailsService,
    private _viewportService: ViewportService
  ) { }

  ngOnInit () {
    this._viewportService.checkViewSizes();
    this.baseClassName = this._viewportService.sizeClassName();
    this.getOfferDetails();
  }

  getOfferDetails () {
    this._propertyDetailsService
      .getOfferDetails( this.id )
      .subscribe( res => {
        this.details = res;
        const propertyAcquistionExpenses = this.details.propertyAcquistionExpenses;
        propertyAcquistionExpenses.map( ( expense, ind ) => {
          let expenseValue = this.getValue( expense.propertyAcquisitionExpenseTypeID );
          if ( expenseValue ) {
            this.chartValues = [... this.chartValues, { value: expenseValue, color: CAPITAL_BREAKDOWN_COLOR_SEQUENCE[ind], name: expense.propertyAcquisitionExpenseTypeName === 'Legal Cost' ? 'LLC Legal Fees' : expense.propertyAcquisitionExpenseTypeName }];
          }
        } );
      } );
  }

  formatDate ( date ) {
    return moment( date ).format( 'DD MMMM YYYY' );
  }

  getValue ( id ) {
    const propertyAcquistionExpenses = this.details.propertyAcquistionExpenses;
    return propertyAcquistionExpenses.filter( expense => {
      return expense.propertyAcquisitionExpenseTypeID === id;
    } )[0].expenseAmount || null;
  }
}
