import { Component, OnInit, HostListener, Input } from '@angular/core';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import * as moment from 'moment';

@Component( {
  selector: 'documents-content',
  templateUrl: './documents-content.component.html',
  styleUrls: ['./documents-content.component.scss']
} )
export class DocumentsContentComponent implements OnInit {
  @Input( 'propertyId' ) id: number;
  details;
  baseClassName;

  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
      this.baseClassName = this._viewportService.sizeClassName();
    }, 250 );
  }

  constructor (
    private _propertyDetailsService: PropertyDetailsService,
    private _viewportService: ViewportService
  ) { }

  ngOnInit () {
    this._viewportService.checkViewSizes();
    this.baseClassName = this._viewportService.sizeClassName();
    this.getDocumentDetails();
  }

  getDocumentDetails () {
    this._propertyDetailsService
      .getDocumentDetails( this.id )
      .subscribe( res => {
        this.details = res;


      } );
  }

  formatDate ( date ) {
    return moment( date ).format( 'DD MMMM YYYY' );
  }

  getIcon ( ext ) {
    const extension = ext.toLowerCase();
    if ( extension === 'pdf' ) {
      return 'PDF.svg';
    } else if ( extension === 'jpg' || extension === 'jpeg' ) {
      return 'JPG.svg';
    } else if ( extension === 'png' ) {
      return 'PNG.svg';
    } else if ( extension === 'xls' || extension === 'xlsx' ) {
      return 'XLS.svg';
    } else {
      return 'DOC.svg';
    }
  }
}
