import { Component, OnInit, HostListener } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ViewportService } from '@shared/services';

@Component( {
  selector: 'app-about-us',
  templateUrl: './about-us.component.html',
  styleUrls: ['./about-us.component.scss']
} )
export class AboutUsComponent implements OnInit {
  baseClassName;

  @HostListener( 'window:resize', ['$event'] )
  onResize () {
    setTimeout( () => {
      this._viewportService.checkViewSizes();
    }, 250 );
  }

  constructor (
    private title: Title,
    private _viewportService: ViewportService
  ) {
    this._viewportService.updatingView.subscribe( () => {
      this.baseClassName = this._viewportService.sizeClassName();
    } );
  }

  toggleClass ( e ) {
    e.preventDefault();
    const container = e.target.parentNode;
    const cardBody = container.parentNode.previousSibling;
    const content = cardBody.lastChild;

    content.classList.toggle( 'expanded' );
  }
  ngOnInit () {
    this.title.setTitle( `Partbnb - About Us` );
    this._viewportService.checkViewSizes();
    this.baseClassName = this._viewportService.sizeClassName();
    console.log( this.baseClassName );
  }

  getBanner () {
    return this.baseClassName === 'is-mobile' ? '/assets/about-us/Our_Vision_sm.jpg' : '/assets/about-us/Our_Vision_lg.jpg';
  }
}
