import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PropertiesComponent } from './pages/properties/properties.component';
import { ExecutiveLayout } from '@pages/layouts';
import { PropertyDetailsComponent } from './pages/property-details/property-details.component';
import { PurchaseComponent } from './pages/purchase/purchase.component';
import { HomeComponent } from './pages/home/home.component';
import { HowDoesItWorkComponent } from './pages/how-does-it-work/how-does-it-work.component';
import { CornerStoneClubComponent } from './pages/corner-stone-club/corner-stone-club.component';
import { WhyInvestComponent } from './pages/why-invest/why-invest.component';
import { AuthGuard } from '@shared/guards';
import { HelpCenterComponent } from '@modules/main-app/pages/help-center/help-center.component';
import { ReturnsComponent } from './pages/returns/returns.component';
import { SellingExitingComponent } from './pages/selling-exiting/selling-exiting.component';
import { WhoDoesItSuitComponent } from './pages/who-does-it-suit/who-does-it-suit.component';
import { StayingPropertyComponent } from './pages/staying-property/staying-property.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { ManagedComponent } from './pages/managed/managed.component';
import { MarketplaceComponent } from './pages/marketplace/marketplace.component';
import { OurFeesComponent } from './pages/our-fees/our-fees.component';
import { ComparisonComponent } from './pages/comparison/comparison.component';
import { OfferingInformationComponent } from './pages/offering-information/offering-information.component';
import {
  RegulationDOfferingsPalmetoPalaceComponent,
  RegulatoryFillingsComponent
} from './pages/offering-information/offering-information-child';
import { PolicyComponent } from './pages/policy/policy.component';
import { MyProfileComponent } from '@shared/components/my-profile/my-profile.component';
import { HowItWorksComponent } from './pages/how-it-works/how-it-works.component';
import { OwnersAgreementComponent } from './pages/owners-agreement/owners-agreement.component';
import { FaqsComponent } from './pages/faqs/faqs.component';
import { FeaturesAndBenefitsComponent } from './pages/features-and-benefits/features-and-benefits.component';
import { FeesAndCostComponent } from './pages/fees-and-cost/fees-and-cost.component';

export const routes: Routes = [
  {
    path: '',
    component: ExecutiveLayout,
    data: { module: 'mainApp' },
    children: [
      { path: '', redirectTo: 'home', pathMatch: 'full' },
      {
        path: 'home',
        component: HomeComponent,
        data: { pageTitle: 'Partbnb - Home', pageName: 'Home' }
      },
      {
        path: 'our-process',
        component: HowItWorksComponent,
        data: { pageTitle: 'Partbnb - Our Proces', pageName: 'Our Proces' }
      },
      {
        path: 'owners-agreement',
        component: OwnersAgreementComponent,
        data: { pageTitle: 'Partbnb - Owners Agreement', pageName: 'Owners Agreement' }
      },
      {
        path: 'faqs',
        component: FaqsComponent,
        data: { pageTitle: 'Partbnb - FAQs', pageName: 'FAQs' }
      },
      {
        path: 'features-and-benefits',
        component: FeaturesAndBenefitsComponent,
        data: { pageTitle: 'Partbnb - Features & Benefits', pageName: 'Features & Benefits' }
      },
      {
        path: 'fees-and-costs',
        component: FeesAndCostComponent,
        data: { pageTitle: 'Partbnb - Fees / Costs', pageName: 'Fees / Costs' }
      },
      {
        path: 'corner-stone-club',
        component: CornerStoneClubComponent,
        data: { pageTitle: 'Partbnb - Cornerstone Club', pageName: 'Cornerstone Club' }
      },
      {
        path: 'why-invest',
        component: WhyInvestComponent,
        data: { pageTitle: 'Partbnb - Why Invest', pageName: 'Invest' }
      },
      {
        path: 'properties',
        component: PropertiesComponent,
        data: { pageTitle: 'Partbnb - Properties', pageName: 'Properties' }
      },
      {
        path: 'help',
        component: HelpCenterComponent,
        data: { pageTitle: 'Partbnb - Help Center', pageName: 'Help' }
      },
      {
        path: 'returns',
        component: ReturnsComponent,
        data: { pageTitle: 'Partbnb - Earning Returns', pageName: 'Returns' }
      },
      {
        path: 'selling-exiting',
        component: SellingExitingComponent,
        data: { pageTitle: 'Partbnb - Selling Parts', pageName: 'Selling' }
      },
      {
        path: 'who',
        component: WhoDoesItSuitComponent,
        data: { pageTitle: 'Partbnb - Who It Suit', pageName: 'Who' }
      },
      {
        path: 'about',
        component: AboutUsComponent,
        data: { pageTitle: 'Partbnb - About Us', pageName: 'About' }
      },
      {
        path: 'manage',
        component: ManagedComponent,
        data: { pageTitle: 'Partbnb - Property Management', pageName: 'Manage' }
      },
      {
        path: 'stay-property',
        component: StayingPropertyComponent,
        data: { pageTitle: 'Partbnb - Staying', pageName: 'Stay' }
      },
      {
        path: 'marketplace',
        component: MarketplaceComponent,
        data: {
          pageTitle: 'Partbnb - Our MarketPlace',
          pageName: 'Marketplace'
        }
      },
      {
        path: 'our-fees',
        component: OurFeesComponent,
        data: { pageTitle: 'Partbnb - Fees/Costs', pageName: 'Fees' }
      },
      {
        path: 'comparison',
        component: ComparisonComponent,
        data: { pageTitle: 'Partbnb - Compare Options', pageName: 'Comparison' }
      },
      {
        path: 'offerings',
        component: OfferingInformationComponent,
        data: {
          pageTitle: 'Partbnb - Offering Information',
          pageName: 'Offerings'
        },
        children: [
          {
            path: 'palmetto-place',
            component: RegulationDOfferingsPalmetoPalaceComponent
          },
          {
            path: 'regulatory-fillings',
            component: RegulatoryFillingsComponent
          }
        ]
      },
      {
        path: 'property',
        data: { pageTitle: 'Partbnb - Property Details', pageName: 'Property' },
        children: [
          {
            path: ':id',
            component: PropertyDetailsComponent
          },
          { path: '**', component: PropertyDetailsComponent }
        ]
      },
      {
        path: 'policy',
        component: PolicyComponent,
        data: { pageTitle: 'Partbnb - Privacy Policy', pageName: 'Policy' }
      },
      {
        path: 'my-profile',
        component: MyProfileComponent,
        data: {
          pageTitle: 'Partbnb - My Profile',
          pageName: 'My Profile'
        },
        canActivate: [AuthGuard]
      },
      {
        path: 'test-plaid',
        component: PurchaseComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MainAppRoutingModule {}
