import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import groupBy from 'app/shared/utilities/groupBy';
import { Title } from '@angular/platform-browser';

import { FilterFaq } from '@shared/pipes/FilterFaq.pipe';
import { FaqsService } from '@shared/api';
import { Subject } from 'rxjs';
import 'rxjs/add/operator/distinctUntilChanged';

@Component({
  selector: 'app-faqs',
  templateUrl: './faqs.component.html',
  styleUrls: ['./faqs.component.scss'],
  providers: [FilterFaq]
})
export class FaqsComponent implements OnInit {


  items: any[] = [];
  categories: string[];
  searchInput: string = '';
  faq: Object[];
  hideCategories = false;
  @ViewChild('search') searchElement: ElementRef;

  searchTerm$ = new Subject<string>();
  constructor(private filterFaq: FilterFaq, private title: Title, private faqsService: FaqsService) {

  }

  search(keyword) {
    this.items =  this.filterFaq.transform(JSON.parse(JSON.stringify(this.faq)), keyword, [
      "sectionName",
      "question",
      "answer"
    ], 0.5);
    
    this.items.forEach(i => {
      i.question = this.highlight(i.question, keyword);
      i.answer = this.highlight(i.answer, keyword);
    });

    if(keyword.length > 0)
      this.hideCategories = true;
    else{
      this.hideCategories = false;
      if(this.categories.length > 0)
          this.selectCategory(this.categories[0]);
    }

  }

   highlight(paragraph, search) {
    return paragraph.replace(new RegExp(search, "gi"), (match) => `<mark>${match}</mark>`);
  }


  model:any;

  ngOnInit() {
      this.title.setTitle(`Partbnb - FAQs`);

      this.faqsService.get().subscribe(data => {
        this.faq = data;
        this.categories = Object.keys(groupBy(this.faq, 'sectionName'));

        if(this.categories.length > 0)
          this.selectCategory(this.categories[0]);
      });

      this.searchTerm$.debounceTime(400)
      .distinctUntilChanged()
      .subscribe(keyword => this.search(keyword))

      setTimeout(()=>{ 
        this.searchElement.nativeElement.focus();
      },0);
  }

  selectedItem;
  select(item){
    this.selectedItem = item;
  }

  selectedCategory;
  selectCategory(category){
    this.selectedCategory = category;
    this.items =  this.filterFaq.transform(JSON.parse(JSON.stringify(this.faq)), category, [
      "sectionName"
    ]);
    if(this.items.length > 0)
      this.select(this.items[0]);
  }


  images = [
    {name: 'Getting Started', src: '/assets/img/faqs/categories/icn-started'},
    {name: 'Buying', src: '/assets/img/faqs/categories/icn-buying'},
    {name: 'Selling', src: '/assets/img/faqs/categories/icn-selling'},
    {name: 'Management', src: '/assets/img/faqs/categories/icn-management'},
  ];
  getImage(category, isActive){
    var image = this.images.find(f => f.name == category)
    if(image)
      return image.src + (isActive ? '-white.svg' : '.svg' );
  }

}
